<template>
  <div class="wrapper wrapper-content animated fadeInRight">
      <div class="ibox-content" id="map" style="position: relative; height: 100vh"></div>
  </div>
</template>
	<style type="text/css" scope>
		label{max-width:none}
		#l-map{height:300px;width:100%;}
		#r-result{width:100%; font-size:14px;line-height:20px;}
	</style>
<script>
export default {
  name: 'mapPage' ,
  mounted(){
	    //   $(function() {
        //     $('#map').click(function () {
        //         console.log('执行了')
        //     });
        // });
     /* console.log(999)
      //百度地图加载完成会调用这里，这里就可以正常初始化了
      var map = new BMap.Map('map')   // 创建Map实例
      map.centerAndZoom(new BMap.Point(121.76, 31.05), 11)  // 初始化地图,设置中心点坐标和地图级别
      map.addControl(new BMap.MapTypeControl())   // 添加地图类型控件
      //    map.setCurrentCity("上海");          // 设置地图显示的城市 此项是必须设置的
      map.enableScrollWheelZoom(true)    // 开启鼠标滚轮缩放
      */
      // 百度地图API功能	
	var map = new BMap.Map("map");
	map.centerAndZoom(new BMap.Point(116.417854,39.921988), 10);
	map.enableScrollWheelZoom();  
	
					var data_info2 = [
["留声机摄影工作室","北京市大兴区溪水园小区","192072704"],
["神奇会馆","北京市昌平区天通苑北一区","361515107"],
["Mirror-Studio","北京市朝阳区中弘北京像素北区","252841968"],
["帝都漫迷网实景棚","北京市石景山区模式口28号院","437239016"],
["TH工作室","北京市通州区水仙园","96499046"],
["万事屋Studio","北京市朝阳区万事屋摄影工作室","WX13581836093"],
["大魔王影棚","北京市朝阳区惠安轩","156912447"],
["私语工作室","北京市丰台区东长街30号","QQ396261256"],
["TAG会馆","北京市通州区漷县镇","552172688"],
["无限演绎摄影工作室","北京市西城区湾子街2号楼","465302892"],
["宫娘摄影棚","北京市昌平区于辛家园","373137335"],
["九十八影棚","北京市朝阳区燕华苑1座","274751817"],
["极光映像工作室","北京市丰台区彩虹城4区","190011401"],
["琅琊阁古风棚","北京市大兴区龙湖时代天街东区4号楼","530363651"],
["平流层工作室","北京市西城区格调小区1号楼","616907363"],
["风色言语工作室","北京市朝阳区蓝调庄园","384192972"],
["戚墨画摄影工作室","北京市朝阳区蓝调庄园","0"],
["唯美Cosplay摄影","北京市通州区七夕园婚纱摄影基地","213062167"],

];
	var opts = {
				width : 250,     // 信息窗口宽度
				height: 80,     // 信息窗口高度
				title : "信息窗口" , // 信息窗口标题
				enableMessage:true,//设置允许信息窗发送短息
        
			   };


	var myGeo = new BMap.Geocoder();
	//浏览器定位
	var geolocation = new BMap.Geolocation();
	geolocation.getCurrentPosition(function(r){
		if(this.getStatus() == BMAP_STATUS_SUCCESS){
			var mk = new BMap.Marker(r.point);
			map.addOverlay(mk);
			map.panTo(r.point);
		}
		else {
			alert('failed'+this.getStatus());
		}        
	},{enableHighAccuracy: true});

 //批量转义
	  for(var i=0;i<data_info2.length;i++){
  	geocodeSearch(data_info2[i][1],data_info2[i][0]);
  }
	function geocodeSearch(add,labels){		
		myGeo.getPoint(add, function(point){
			if (point) {				
				var address = new BMap.Point(point.lng, point.lat);
				addMarker(address,new BMap.Label(labels,{offset:new BMap.Size(20,-10)}));
			}
		}, "北京市");
	}

	// 编写自定义函数,创建标注
	function addMarker(point,label){
		console.log(label)
		var marker = new BMap.Marker(point);
		map.addOverlay(marker);
		marker.setLabel(label);
	}
	function addClickHandler(content,marker){
		marker.addEventListener("click",function(e){
			openInfo(content,e)}
		);
	}
	function openInfo(content,e){
		var p = e.target;
		var point = new BMap.Point(p.getPosition().lng, p.getPosition().lat);
		var infoWindow = new BMap.InfoWindow(content,opts);  // 创建信息窗口对象 
		map.openInfoWindow(infoWindow,point); //开启信息窗口
	}
  }
}

</script>



